export interface DialogData {
  message: string;
}
@Component({
  selector: 'app-content-provider',
  templateUrl: './content-provider.component.html',
  styleUrls: ['./content-provider.component.css']
})

export class ContentProviderComponent implements OnInit {
  cps;
  message: string = "Please press OK to continue.";

  
  constructor(public dialog: MatDialog) { 
    this.cps = data;
  }

  ngOnInit(): void {
  }

  openDeleteConfirmModal(): void {
    const dialogRef = this.dialog.open(CPDeleteConfirmDialog, {
      data: {message: this.message},
      width: '40%'
    });
  
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  openEditConfirmModal(): void {
    const dialogRef = this.dialog.open(AddContentProviderComponent, {
      width: '60%',disableClose: true,
      data: {message: this.message}
    });
  
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  openNewCPModal(): void {
    const dialogRef = this.dialog.open(AddContentProviderComponent,{
      width: '60%',disableClose: true 
    });

  }
  
  }


  
@Component({
  selector: 'cp-edit-confirm-dialog',
  templateUrl: 'cp-edit-confirm-dialog.html',
  styleUrls: ['./content-provider.component.css']
})
export class CPEditConfirmDialog {

  constructor(
    public dialogRef: MatDialogRef<CPEditConfirmDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onCancelUpload(): void {
    this.dialogRef.close();
  }

  onConfirmUpload(): void {
    this.dialogRef.close();
  }

}


